.loadWrap {
    font-size: 50px;
    min-height: 600px; line-height: 600px;
    text-align: center;
    .loadicon {
        animation: loadicon 3s infinite linear;
    }
    @keyframes loadicon {
        0% {
            transform: rotate(0deg);
        }
        100% {
            transform: rotate(360deg);
        }
    }
}