.detail-box {
    text-align: center;
    padding: 100px 0px;
    .detail-img {
        border: 5px solid #fff;
        display: inline-block;
        vertical-align: middle;
        img {
            width: 100%;
        }
    }
    .detail-txt {
        display: inline-block;
        width: 500px;
        vertical-align: middle;
        text-align: left;
        padding-left: 20px;
        .detail-title {font-size: 60px;}
        .detail-year {
            font-size: 30px;
            color: #ccc;
        }
        .detail-genres {
            font-size: 30px;
            padding: 0px 0px 30px 0px;
            color: #428bca;
            p {
                display: inline-block;
                padding-right: 15px;
            }
        }
        .detail-summary {font-size: 20px;}
    }
}