.movies {
    // 1. float 방식
    // width:25%; float:left;
    
    // 2. flex 방식
    // width: 25%;

    // 3. grid 방식
    margin: 20px 10%;
    img {width: 100%; border: 3px solid #fff;}
    .movies_data {
        h3 {
            font-size: 20px; 
            color: #fff;
        }
        h5 {
            color: #ccc;
        }
        ul {list-style: none;
            li {color: #428bca; font-weight: bold; padding-right: 20px; display: inline-block;}
        }
        p {
            color: #fff;
        }
    }

}